import React, { useEffect, useState } from "react";
import { BrowserRouter, Routes, Route, Link, Navigate } from "react-router-dom";

import logo from './logo.svg';

import PublicRoute from "./Router/PublicRoute";
import PrivateRoute from "./Router/PrivateRoute";
import AuthRoute from "./Router/AuthRoute";

import Login from "./templates/auth/login";
import SignUp from "./templates/auth/signup";
import Main from "./templates/main/main";
import NotFound from "./templates/NotFound";

import Cookies from "universal-cookie";

function App()
{
  return (
        <BrowserRouter >
            <Routes>
                <Route path="/login" restricted={true} element={ <PublicRoute> <Login/> </PublicRoute>} />          {/*   */}
                <Route path="/signup" restricted={true} element={ <PublicRoute> <SignUp/> </PublicRoute>} />        {/*   */}
                <Route path="/*" restricted={false} element={ <PrivateRoute> <Main/> </PrivateRoute> } />           {/*   */}
                <Route element={ NotFound } />
            </Routes>
    </BrowserRouter>
  );
}

export default App;
