import React, { useState } from "react";
import { Navigate, Link } from "react-router-dom";

import axios from "axios";
import Cookies from "universal-cookie";

import './auth.css';

import LoginImage from "../../images/DeepFramework_login.png";
import Logo_Image from "../../images/logo_3.png";

function Login()
{
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const appKeyPress = (e) => {
        if (e.key === "Enter") {
            handleClick();
        }
    };

    const handleClick = () => {
        try
        {
            axios.post("http://192.168.0.179:8000/api/token/", {'username': email, 'password': password}).then((res) =>
            {
                if(res.data["token"] !== null)
                {
                    var token = res.data["token"];

                    const cookies = new Cookies();
                    cookies.set('DF_TOKEN', token, {path:"/"});

                    setEmail("");
                    setPassword("");
                    window.location.replace("/");
                }

            }).catch(error =>
            {
                alert("   .");
            });

        }
        catch (err)
        {
            console.log(err);
        }
    };

    return (
        <div className="login">

            {/*     */}
            <div className="login_left">
                <div className="top">
                    <img src={Logo_Image} alt=" "/>&nbsp;
                    <span style={{ color: "#448FFF", fontSize: "2vw"}}>Deep</span>
                    <span style={{ color: "white", fontSize: "2vw"}}>Framework</span>
                </div>

                <div className="bottom">
                    <img src={LoginImage} alt=" "></img>
                </div>
            </div>

            {/*     */}
            <div className="login_right">
                <div className="login_form">
                    <div className="login-header" style={{'textAlign':'center'}}>Deep Framework</div>

                    {/*    - ,  */}
                    <div className="login-body">
                        <input
                            value={email}
                            onChange={({ target: { value } }) => setEmail(value)}
                            type="text"
                            placeholder=" ID"
                            onKeyPress={appKeyPress}
                        />
                        <input
                            value={password}
                            onChange={({ target: { value } }) => setPassword(value)}
                            type="password"
                            placeholder=" Password"
                            onKeyPress={appKeyPress}
                        />
                    </div>

                    {/*     - ,  */}
                    <div className="button" style={{'marginTop':'20px'}}>
                        <button onClick={handleClick}>Log in</button>

                        <Link to="/signup" >
                            <button style={{fontWeight: "400", 'backgroundColor':'#707070'}}>Sign Up</button>
                        </Link>
                    </div>

                </div>
            </div>
        </div>
    );
}

export default Login;