import React, { useState } from "react";
import { Route, Link } from "react-router-dom";

import axios from "axios";
import Cookies from "universal-cookie";

import './auth.css';

import LoginImage from "../../images/DeepFramework_login.png";
import Logo_Image from "../../images/logo_3.png";

function SignUp()
{
    const [duplicateCheck,setDuplicateCheck] = useState(false); //   
    const [id,setID] = useState('');
    const [password,setPassword] = useState('');
    const [passwordConfirm,setPasswordConfirm] = useState('');
    const [idError,setIDError] = useState(false);
    const [passwordError,setPasswordError] = useState(false);
    const [passwordConfirmError,setPasswordConfirmError] = useState(false);

    const handleClick = () => {
        if(idError || passwordError || passwordConfirmError)
        {
            alert("  ");
            return;
        }
        if(id === '' || password === '' || passwordConfirm === '')
        {
            alert(" ");
            return;
        }

        if(duplicateCheck === false)
        {
            alert("ID   ");
            return ;
        }
        try {
            const obj = {
                id : id,
                password : password
            };

            axios({
                method: 'post',
                url: '/api/signup',
                data: obj,
            }).then(function (res) {
                alert(" .")
                setDuplicateCheck(false);
                setID('');
                setPassword('');
                setPasswordConfirm('');

                window.location.reload();
            });
        } catch (err) {
            console.log(err);
        }
    };

     const idCheck = ()=>{
        if(id === '')
        {
            alert("ID  ")
            return
        }
        if(idError)
        {
            alert("ID   ")
            return
        }
        try {
            const obj = {
                id : id,
            };
            axios({
                method: 'post',
                url: '/api/user_id_check',
                data: obj,
            }).then(function (res) {
                if(res.data === 'pass')
                {
                    alert("  id.")
                    setDuplicateCheck(true);
                }
                else
                {
                    alert(" id.")
                    setID('')
                }
            });
        } catch (err) {
            console.log(err);
        }
    }


    const appKeyPress = (e) => {
        if (e.key === "Enter") {
            handleClick();
        }
    };

    const onChangeID = (e) =>{
        const userIdRegex = /^[A-Za-z0-9+]{5,20}$/;
        if ((!e.target.value || (userIdRegex.test(e.target.value)))) setIDError(false);
        else setIDError(true);
        setID(e.target.value);
        setDuplicateCheck(false);
    }

    const onChangePassword= (e) =>{
        const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[$@$!%*?&])[A-Za-z\d$@$!%*?&]{8,}/;
        if ((!e.target.value || (passwordRegex.test(e.target.value)))) setPasswordError(false);
        else setPasswordError(true);
        setPassword(e.target.value);
    }

    const onChangePasswordConfirm = (e) =>{
        if (password === e.target.value) setPasswordConfirmError(false);
        else setPasswordConfirmError(true);
        setPasswordConfirm(e.target.value);
    }

    return (
        <div className="login">

            {/*     */}
            <div className="login_left">
                <div className="top">
                    <img src={Logo_Image} alt=" "/>&nbsp;
                    <span style={{ color: "#448FFF", fontSize: "2vw"}}>Deep</span>
                    <span style={{ color: "white", fontSize: "2vw"}}>Framework</span>
                </div>

                <div className="bottom">
                    <img src={LoginImage} alt=" "></img>
                </div>
            </div>

            {/*     */}
            <div className="login_right">
                <div className="login_form">
                    <div className="login-header" style={{'textAlign':'center'}}>Create Account</div>

                    {/*    */}
                    <div className="login-body">

                        {/*    -   */}
                        <div style={{width:'100%', marginBottom:"5px"}}>
                            <input  value={id} onChange={onChangeID} type="text" placeholder=" ID" style={{width:'80%'}} onKeyPress={appKeyPress} />
                            <button onClick={idCheck}  style={{ width:'20%', 'marginTop':'15px', 'marginLeft':'15px',height:'55px', width:"80px", fontSize:"15px", fontWeight: "400", float:'right'}}>Check</button>
                            <p style={{fontSize:"12px", color: idError ? "white" : "transparent"}}>5~20  ,  (_),(-)  .</p>
                        </div>

                        {/*    -   */}
                        <input value={password} onChange={onChangePassword} type="password" placeholder=" Password" onKeyPress={appKeyPress} />
                        <p style={{fontSize:"12px", color: passwordError ? "white" : "transparent"}}>8 ,  ,  </p>

                        {/*    -    */}
                        <input style={{}} value={passwordConfirm} onChange={onChangePasswordConfirm} type="password" placeholder=" PasswordConfirm" onKeyPress={appKeyPress} />
                        <p style={{fontSize:"12px", color: passwordConfirmError ? "white" : "transparent"}}>  .</p>

                        {/*    -  ( ,  ) */}
                        <div className="login-button" style={{'marginTop':'30px'}}>
                           <button onClick={handleClick}>Create Account</button>

                           <Link to="/login" >
                                <button style={{'marginTop':'15px', fontWeight: "400", 'backgroundColor':'#707070'}}>Cancel</button>
                            </Link>
                        </div>
                    </div>


                </div>
            </div>
        </div>
    );
}

export default SignUp;